<template>
    <v-snackbar
      :timeout="timeout"
      :value="true"
      absolute
      bottom
      centered
      color="deep-purple accent-4"
    >
      <strong>{{snackbartext}}</strong>
    </v-snackbar>
</template>

<script>
export default {
    name:'Snackbar',
    data(){
     return{
         timeout:2000
     }
    },
    props:{
        snackbartext:String
    }
}
</script>