<template>
  <div>
    <NavBar />
    <Password v-if="user.logincount === 1"/>
     <Alert :msg="msg" color="dark" v-if="msg"/>
    <AddSale />
    <Table />
    <v-dialog v-model="expense" width="300">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="deep purple" dark v-bind="attrs" v-on="on" rounded style="margin-top:20px;">
          Add Expense
        </v-btn>
      </template>
      <v-card>
        <v-card-title>New Expense</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col
              cols="12"
              md="6"
              sm="6"
              >
              <v-text-field
              label="expense name"
              v-model="expense_name"
              >
                
              </v-text-field>
              </v-col>
              <v-col
              cols="12"
              md="6"
              sm="6"
              >
              <v-text-field
              label="amount"
              v-model="amount"
              >
              
              </v-text-field>
              </v-col>
            </v-row>
            <v-btn color="primary" dark rounded @click="addExpense">
          Add
        </v-btn>
          </v-container>
        </v-card-text>
      </v-card>
    </v-dialog>
    <snackbar :snackbartext="snackbartext" v-if="snackbar" />
    <div class="stext-left" style="margin-top: 30px">
      <v-dialog v-model="dialog" width="600">
        <template v-slot:activator="{ on, attrs }">
          <v-badge
            color="black"
            :content="pendingsales.length"
            overlap
            v-if="pendingsales.length > 0"
          >
            <v-btn
              color="orange"
              dark
              v-bind="attrs"
              v-on="on"
              v-if="pendingsales.length > 0"
              rounded
            >
              Pending Orders
            </v-btn>
          </v-badge>
        </template>

        <v-card class="mx-auto" max-width="600">
          <v-toolbar color="teal" dark>
            <v-toolbar-title>Pending Orders</v-toolbar-title>
          </v-toolbar>

          <v-list>
            <v-list-group
              v-for="item in items"
              :key="item.title"
              v-model="item.active"
              :prepend-icon="item.action"
              no-action
            >
              <template v-slot:activator>
                <v-list-item-content>
                  <v-list-item-title v-text="item.title"></v-list-item-title>
                </v-list-item-content>
              </template>

              <v-list-item v-for="child in item.items" :key="child.title">
                <v-list-item-content>
                  <v-list-item-title v-text="child.title"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-group>
          </v-list>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import NavBar from "../components/Navbar.vue";
import AddSale from "../components/AddSale.vue";
import Table from "../components/Table.vue";
import snackbar from "../components/Snackbar.vue";
import Password from '../components/AlertPassword.vue'
import Alert from '../components/Alert.vue';
import { mapGetters, mapActions } from "vuex";
import store from "../store";
export default {
  name: "DashboardView",
  computed: mapGetters(["pendingsales",'user']),
  beforeRouteEnter: (to, from, next) => {
    if (!store.state.auth.token) {
      next("/");
    } else {
      next();
    }
  },
  components: {
    NavBar,
    AddSale,
    Table,
    snackbar,
    Password,
    Alert
  },
  mounted() {
    this.$root.$on('ordernumber', data => {
        this.msg = `Order Number: ${data}`
        this.msg = null
    });
},
  data() {
    return {
      dialog: false,
      snackbartext: "This is a snackbar text",
      snackbar: false,
      expense:false,
      msg:null,
      expense_name:'',
      amount:'',
      items: [
        {
          action: "fa-brands fa-product-hunt",
          items: [{ title: "List Item" }],
          title: "Sale ID",
        },
      ],
    };
  },
  methods:{
    ...mapActions(['addExpenseDb']),
  async addExpense(){
     const exp = {
       expense_name:this.expense_name,
       amount:this.amount
     }
     try {
     const res = await this.addExpenseDb(exp)
      this.expense = false;
      this.snackbar = true
      this.snackbartext = res;
      this.expense_name = ''
      this.amount = '';  
     } catch (error) {
        this.snackbar = true;
        this.snackbartext = 'Unable to add Expense'     
     }
   }
  }
};
</script>
