<template>
<v-sheet>
    <v-data-table
    :headers="headers"
    :items="sales"
     hide-default-footer
    sort-by="total"
    class="elevation-1 table"
    dark
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.product_name"
                      label="Product name"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.quantity"
                      label="Quantity"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="red darken-1"
                text
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
  <Button color="green" icon="fa-solid fa-cart-plus" v-if="sales.length> 0" :total="total" :content="sales.length"/>
</v-sheet>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'
  import Button from '../components/Button.vue'
  export default {
    components:{
       Button
    },
    data: () => ({
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: 'Product Name',
          align: 'start',
          sortable: false,
          value: 'product_name',
        },
        { text: 'Price(ksh)', value: 'price' , align:'center'},
        { text: 'Quantity', value: 'quantity' , align:'center'},
        { text: 'Total (ksh)', value: 'total' , align:'center'},
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      editedIndex: -1,
      editedItem: {
        name: '',
        price: 0,
        quantity: 0,
        total: 0,
      },
      defaultItem: {
        name: '',
        price: 0,
        quantity: 0,
        total: 0
      },
    }),

    computed: {
      ...mapGetters(['sales']),
      total(){
        return this.sales.reduce((acc,curr)=>{
          return acc += curr.total
        },0)
      },
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      }
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
    ...mapActions(['editSale','deleteSale']),
      initialize () {
        this.sales
        },
      editItem (item) {
        this.editedIndex = this.sales.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.sales.indexOf(item)
        this.editedItem = Object.assign({}, item)
        const details = {
          sales:this.sales,
          id:this.editedItem.id
        }
        this.dialogDelete = true
        this.deleteSale(details)
      },

      deleteItemConfirm () {
        this.sales.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.sales[this.editedIndex], this.editedItem)
          const id = this.editedItem.id
          const details = {
            sales:this.sales,
            id,
            update:this.editedItem.quantity
          }
          this.editSale(details) 
        } else {
          this.sales.push(this.editedItem)
        }
        this.close()
      }
      }      
  }
</script>
<style scoped>
 .table{
   margin-top: 20px;
   font-size: 50px;
 }
 .table >>> th {
  font-size: 1.8rem !important; 
  font-weight: bolder;
}
.table >>> tr>td{
  font-size:1.2rem !important;
}
</style>