<template>
  <v-alert prominent type="error">
    <v-row align="center">
      <v-col class="grow"> You are using the default pin. Please generate a new one</v-col>
      <v-col class="shrink">
        <v-dialog transition="dialog-bottom-transition" max-width="600" v-model="dialog">
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on">Change Pin</v-btn>
          </template>
          <v-card>
            <v-toolbar color="primary" dark>Generate Pin</v-toolbar>
            <v-card-text>
              <v-text-field
            v-model="pin"
            label="PIN"
            @click:append="show3 = !show3"
          ></v-text-field>
            </v-card-text>
            <v-card-actions>
              <v-btn @click="savepin" block color="orange" dark>Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-col>
    </v-row>
  </v-alert>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
export default {
  name: "PasswordAlert",
  computed: mapGetters(["user"]),
  data() {
    return {
      dialog: false,
      pin: null,
      disabled:false,
      show3: false,
      rules: {
          required: value => !!value || 'Required.',
          max: v => v.length === 4 || 'Max 4 characters',
          }
    };
  },
  methods: {
    ...mapActions(['changePin']),
   async savepin() {
   const res = await this.changePin({id:this.user._id,pin:this.pin})
    if(res.msg === true){
      this.$router.push('/')
    }else{
      console.log('Something went wrong')
    }
    this.disabled = true
    },
  },
};
</script>
