<template>
<div>
  <v-form
       ref="form"
       @submit.prevent="addSale"
       style="width:100%;margin-top:20px"
       >
       <v-autocomplete
        :items="allProducts"
        color="dark"
        prepend-icon="fa-solid fa-magnifying-glass-plus"
        item-text="product_name"
        hide-no-data
        label="Start typing to add a new sale"
        v-model="item"
      ></v-autocomplete>
       </v-form>
       <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <v-card>
            <v-card-title>
              <span class="text-h5">New Item</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="product_name"
                      label="Product name"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="quantity"
                      label="Quantity"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="red darken-1"
                text
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="add"
                @keypress="add"
              >
                Add
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        </div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex'
import {v4 as uuidv4} from 'uuid'
export default {
    name:'addSaleComponent',
    computed:mapGetters(['allProducts']),
    created(){
      this.fetchProducts()
    },
    data(){
        return{
        item:'',
        product_name:'',
        quantity:'',
        price:'',
        dialog:false    
        }
    },
    methods:{
      ...mapActions(['fetchProducts','newSale']),
      close(){
        this.dialog = false;
      },
        addSale(){
        // find the product from the products state  
         const product= this.allProducts.find(product=>product.product_name === this.item)
         this.dialog = true
         this.product_name = product.product_name,
         this.quantity = 1
         this.price = product.price
         //create a new sale object from the product and give an id
        },
        add(){
          const sale = {
           id:uuidv4(),
           product_name:this.product_name,
           price:this.price,
           quantity:this.quantity
         }
         sale['total'] = sale.price * sale.quantity
         this.newSale(sale)
         this.dialog = false
         this.item = ''
        }
    }
}
</script>
