<template>

    <div>
      <v-dialog
      v-model="dialog"
        max-width="600"
        persistent
    >
    <template v-slot:activator="{ on, attrs }">
          <div class="cart-div">
            <v-badge
      :content="content"
      color="black"
      
      overlap
      >
       <v-btn
      class="mx-2"
      fab
      v-bind="attrs"
      v-on="on"
      :color="color"
      large
    >
      
      <v-icon style="color:white">
       {{icon}}
      </v-icon>
    </v-btn>   
      </v-badge>
        </div>
         
        
    </template>
    <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Order Summary
        </v-card-title>
          <v-card-text d-flex flex-column>
            <div style="margin-top:20px">
              <h2>Items ({{ content }}):</h2>
              <v-row>
                <div style="margin-top:10px">
                  <v-col v-for="sale in sales" :key="sale.id"
                    >{{ sale.product_name }} x{{ sale.quantity }}
                  </v-col>
                </div>
                <v-divider></v-divider>
                <div>
                  <v-col v-for="sale in sales" :key="sale.product_name">{{
                    sale.total
                  }}</v-col>
                </div>
              </v-row>
            </div>

            <div class="sale-summary">
              <h2>Order Total:</h2>
              <p style="font-size: 25px; font-weight: bolder">
                ksh {{ total }}
              </p>
            </div>
            <p style="margin-top:30px;font-weight: bold; font-size: 20px">
              How will the customer be paying?
            </p>
          </v-card-text>
        <!-- <v-card-text d-flex flex-column>
          <div class="sale-summary">
              <p style="font-weight:bold;font-size:20px;">Items ({{content}}):</p>
              <p style="font-size:18px">ksh {{total}}</p>
          </div>
          <div class="sale-summary">
              <h2>Order Total:</h2>
              <p style="font-size:25px;font-weight:bolder">ksh {{total}}</p>
          </div>
          <p style="margin-top:30px;font-weight:bold;font-size:20px">How will the customer be paying?</p>
        </v-card-text> -->

        <v-divider></v-divider>
        
        <v-card-actions>
          
           <v-btn
              color="warning"
              dark
              @click="addSaleDb"
              :loading="loading"
            >
              Cash
            </v-btn>
          <v-btn
              color="success"
              dark
              @click="mpesa"
            >
              MPESA
            </v-btn>
            <v-btn
              color="black"
              dark
              @click="pendingSales"
            >
              Save for later
            </v-btn>
            <v-btn
              color="error"
              dark
              @click="dialog = false"
            >
              Cancel Order
            </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <snackbar :snackbartext="snackbartext" v-if="showSnackbar"/>
    </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex';
import snackbar from './Snackbar.vue'
export default {
    name:'ButtonComponent',
    computed:mapGetters(['sales','pendingsales','user']),
    components:{
      snackbar
    },
    props:{
        color:String,
        icon:String,
        content:Number,
        total:Number
    },
    data(){
        return{
              dialog: false,
              loading:false,
              showSnackbar:false,
              snackbartext:'',
              timeout:-1,
        }
    },
    methods:{
      ...mapActions(['addpending','resetSales','addDb','generateOrderNo',]),
      
      async pendingSales(){
      const orderNo = await this.generateOrderNo()  
      this.$root.$emit('ordernumber', orderNo)
      const pending = {
        orderNo,
        orders:this.sales
      }
       this.addpending(pending)
       this.resetSales()
       this.pendingsales
       this.dialog = false
      },
     addSaleDb(){
        this.loading = true
         this.sales.forEach(sale=>{
           sale['user'] = this.user.name
            setTimeout(()=>{
         this.loading = false
         this.addDb(sale)
         this.showSnackbar = true;
         this.total = '';
         this.content = '';
        },2000)
         })
         this.snackbartext = 'Sale Completed'
      }
      ,
      mpesa(){
        this.dialog = false
        console.log('Querying MPESA')        
      }
    }

};
</script>
<style scoped>
 .cart-div{
     margin-top: 30px;
 }
 .sale-summary{
     display: flex;
     justify-content: space-between;
     margin-top:20px;
 }
 
</style>