<template>
  <v-sheet>
    <v-app-bar app color="light" dark>
      <div class="d-flex align-center">
        <v-img
          alt="IPOS-logo"
          class="shrink mr-2"
          cover
          src="../assets/logo.png"
          transition="scale-transition"
          width="40"
          @click="showMenu"
          style="cursor: pointer"
        />
      </div>

      <v-spacer></v-spacer>

      <v-dialog v-model="dialog" max-width="600" persistent>
        <template v-slot:activator="{ on, attrs }">
          <v-form ref="form" @submit.prevent="searchDb" style="width: 20%">
            <v-autocomplete
              :items="pendingsales"
              color="white"
              prepend-icon="fa-solid fa-magnifying-glass-plus"
              item-text="orderNo"
              label="Order Lookup"
              v-model="item"
            ></v-autocomplete>
          </v-form>
        </template>
        <v-card>
          <v-toolbar color="teal" dark>
            <v-toolbar-title>Order {{ item }}</v-toolbar-title>
          </v-toolbar>
          <v-card-text d-flex flex-column>
            <div style="margin-top: 20px;">
              <div style="display:flex;justify-content:space-between;margin-bottom:30px;">
                <h2>Items ({{ content }}):</h2>
              <v-icon
                small
        @click="edit"
      >
        fa-solid fa-pen-to-square
      </v-icon>
              </div>
              <v-row>
                <div style="margin-top: 5px">
                  <v-col v-for="sale in sales" :key="sale.id"
                    >{{ sale.product_name }} x{{ sale.quantity }}
                  </v-col>
                </div>
                <v-divider></v-divider>
                <div>
                  <v-col v-for="sale in sales" :key="sale.product_name">{{
                    sale.total
                  }}
                  </v-col>
                </div>
              </v-row>
            </div>

            <div class="sale-summary">
              <h2>Order Total:</h2>
              <p style="font-size: 25px; font-weight: bolder">
                ksh {{ total }}
              </p>
            </div>
            <p style="margin-top: 25px; font-weight: bold; font-size: 20px">
              How will the customer be paying?
            </p>
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-btn color="warning" dark :loading="loading" @click="addSaleDb">
              Cash
            </v-btn>
            <v-btn color="success" dark @click="dialog = false"> MPESA </v-btn>
            <v-btn color="error" dark @click="closeDialog">
              Close
            </v-btn>
            <v-btn color="teal" dark @click="additem">Add Product</v-btn>
          </v-card-actions>
        </v-card>
        <v-dialog
          v-model="editdialog"
          max-width="500px"
        >
          <v-card>
            <v-card-title>
              <span class="text-h5">Edit Item</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row v-for="sale in sales" :key="sale.id">
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                  <v-text-field
                      v-model="sale.product_name"
                      label="Quantity"
                      disabled
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="sale.quantity"
                      label="Quantity"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="editpending"
              >
                Edit Sale
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog
          v-model="addpending"
          max-width="500px"
        >
          <v-card>
            <v-card-title>
              <span class="text-h5">New Item</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-autocomplete
        :items="allProducts"
        color="dark"
        item-text="product_name"
        hide-no-data
        label="Product name"
        v-model="prod"
      ></v-autocomplete>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="3"
                    md="3"
                  >
                    <v-text-field
                      v-model="quantity"
                      label="Quantity"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="red darken-1"
                text
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="addtopending"
              >
                Add
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-dialog>
       
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" absolute temporary dark>
      <v-list-item>
        <v-list-item-avatar>
          <v-img src="https://randomuser.me/api/portraits/men/78.jpg"></v-img>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title style="font-size: 18px">{{
            user.name
          }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <div class="navigation">
        <router-link
          v-for="link in navlinks"
          :key="link.id"
          :to="`${link.route}`"
          style="text-decoration: none; color: white; font-size: 20px"
        >
          {{ link.route }}</router-link
        >
      </div>
      <template v-slot:append>
        <div class="pa-2">
          <v-btn block color="orange darken-2" @click="logout"> Logout </v-btn>
        </div>
      </template>
    </v-navigation-drawer>
    <snackbar :snackbartext="snackbartext" v-if="showSnack"/>
  </v-sheet>
</template>

<script>
import { mapGetters, mapActions} from "vuex";
import snackbar from './Snackbar.vue';
export default {
  name: "NavbarComponent",
  computed: mapGetters(["user", "pendingsales",'allProducts']),
  components:{
    snackbar
  },
  data() {
    return {
      drawer: null,
      item: "",
      dialog: false,
      loading: false,
      addpending: false,
      content: "",
      editdialog:false,
      total: "",
      prod:'',
      quantity:'',
      snackbartext:'',
      showSnack:false,
      sales: [],
      orderNumber: "",
      pendingList: () => {
        return this.pendingsales.map((sale) => sale.orderNo);
      },
      pendingItems: "",
      navlinks: [
        { route: "Dashboard", id: 1, icon: "fa-solid fa-gauge-simple" }
      ],
    };
  // },
  // watch: {
  //   // whenever question changes, this function will run
  //   pendingsales(){
  //     console.log('Pending sales changed')
  //   },
  //   deep:true
   }
   ,
  methods: {
    ...mapActions(["logout", "addDb", "removePending",'addtoPending','getPending','Editpending']),
    showMenu() {
      this.drawer = !this.drawer;
    },
    additem(){
     this.addpending = true
      this.sales = []
    },
    close(){
      this.addpending = false
      this.prod = ''
      this.quantity = ''
      this.sales = []
    },
    addtopending(){
     let product = this.allProducts.find(products=> products.product_name === this.prod);
     product.quantity = this.quantity
     product['total'] = product.quantity * product.price
     product['orderNo'] = this.orderNumber
     //console.log(this.quantity)
     this.addtoPending(product)
     this.addpending = false
    this.sales = []
    this.dialog = false
    
         },
    editpending(){
    
    this.sales.forEach(sale=>{
      sale['total'] = sale.quantity * sale.price
    })
    this.sales.unshift(this.orderNumber)
    this.Editpending(this.sales)  
    this.editdialog = false
    this.sales = []
    this.dialog = false
    
    },
    edit(){
      this.editdialog = true;
      
    },
    closeDialog() {
      this.sales = [];
      this.dialog = false;
    },
    searchDb() {
      this.dialog = true;
      // console.log(this.pendingsales);
      this.pendingsales.forEach((pending) => {
        if (pending.orderNo === this.item) {
          this.orderNumber = pending.orderNo;
          this.content = pending.orders.length;
          this.total = pending.orders.reduce((acc, curr) => {
            return (acc += curr.total);
          }, 0);
          this.pendingItems = pending.orders;
          pending.orders.forEach((sale) => {
            this.sales.push(sale);
          });
        }
      });
    },
    addSaleDb() {
      this.loading = true;
      this.pendingItems.forEach((sale) => {
        sale["user"] = this.user.name;
        setTimeout(() => {
          this.loading = false;
          this.addDb(sale);
        }, 2000);
      });
      this.removePending(this.orderNumber);
      this.dialog = false;
      this.sales = [];
      this.showSnack = true;
      this.snackbartext ='Sale Complete'
    },
    
  },
};
</script>

<style scoped \>
.navigation {
  border: 2px white;
  margin-top: 30px;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.navigation .currentpage {
  color: orange !important;
  border-bottom: 3px solid orange;
}
.sale-summary {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
</style>
